import React, { PropsWithChildren } from "react";

import { ModalFooterProps } from "../Modal.types";
import { useModalContext } from "../ModalContext";

import { DarkSecondaryButton, FooterWrapper, StyledButton } from "./ModalFooter.styles";

const ModalFooter = ({
	primaryButtonText,
	primaryButtonOnClick,
	hidePrimaryButton,
	secondaryButtonText,
	hideSecondaryButton,
	dataQA,
	secondaryButtonOnClick,
	children,
	className,
}: PropsWithChildren<ModalFooterProps>) => {
	const { onRequestClose, variant, size } = useModalContext();
	const primaryButtonColor = variant === "destructive" ? "destructive" : "blue";

	const SecondaryButton = variant?.includes("graphic") ? (
		<DarkSecondaryButton
			type="button"
			dataQA={`${dataQA}-secondary-footer-button-dark`}
			buttonText={secondaryButtonText}
			onClick={secondaryButtonOnClick || onRequestClose}
			variant="secondary"
			size="Medium"
		/>
	) : (
		<StyledButton
			type="button"
			dataQA={`${dataQA}-secondary-footer-button`}
			buttonText={secondaryButtonText}
			onClick={secondaryButtonOnClick || onRequestClose}
			variant="secondary"
			size="Medium"
		/>
	);

	const PrimaryButton = (
		<StyledButton
			type="button"
			dataQA={`${dataQA}-primary-footer-button`}
			buttonText={primaryButtonText}
			onClick={primaryButtonOnClick}
			variant="primary"
			size="Medium"
			primaryButtonColor={primaryButtonColor}
		/>
	);

	return (
		<FooterWrapper
			variant={variant}
			size={size}
			data-qa={`${dataQA}-modal-footer`}
			className={className}
		>
			{children || (
				<>
					{!hideSecondaryButton && (SecondaryButton)}
					{!hidePrimaryButton && (PrimaryButton)}
				</>
			)}
		</FooterWrapper>
	);
};

ModalFooter.displayName = "ModalFooter";
export default ModalFooter;
